import React, {Component} from 'react'
import PropTypes from 'prop-types'

class Rotator extends Component {
  constructor (props) {
    super(props)

    this.state = {
      slides: this.props.slides,
      currentSlide: 0
    }

    this.renderSlides = this.renderSlides.bind(this)
    this.renderSliderControl = this.renderSliderControl.bind(this)
  }

  componentDidMount () {
    this.timer = setInterval(() => {
      console.log(this.state.currentSlide)
      this.setState({currentSlide: (this.state.currentSlide + 1) % this.state.slides.length})
    }, 5000)

    // clearInterval(this.timer)
  }

  componentWillUnmount () {
    clearInterval(this.timer)
  }

  renderSlides (slide, key, current) {
    let isActive = current === key ? 'active' : ''
    return (
      <img src={slide.image} alt={slide.title} key={key} className={isActive} />
    )
  }

  renderSliderControl (key, current) {
    let isActive = this.state.currentSlide === key ? 'active' : ''
    console.log(key)
    return (
      <li key={key} onClick={this.handleSlideControlClick} className={isActive} />
    )
  }

  handleSlideControlClick (e) {
    console.log(e)
  }

  render () {
    console.log(this.state.slides)
    return (
      <div id='rotator'>
        <ul className='slides'>
          {this.state.slides.map((slide, key) => {
            return this.renderSlides(slide, key, this.state.currentSlide)
          })}
        </ul>
        <div className='slider-control'>
          <ul>
            {this.state.slides.map((slide, key) => {
              return this.renderSliderControl(key)
            })}
          </ul>
        </div>
      </div>
    )
  }
}

Rotator.propTypes = {
  slides: PropTypes.array.isRequired
}

export default Rotator



